:root {
  --text-color: #F5FFFA;
  --link-color: red;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  /* border-radius: 15px; */
  height: 100vh;
}

.App-logo {
  height: 2vmin;
  pointer-events: none;
  display: inline-block;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: linear-gradient(#282c34, grey);
  height: 100%; 
  width: 30em;
  display: flex;
  flex-direction: row;
  align-items: top;
  /* display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center; */
  /* font-size: calc(5px + 1vmin);
  color: var(--text-color); */
  border-radius: 15px;
}

.App-name-box {
  background-color: #282c34;
  height: 17em;
  width: 20em;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  font-size: calc(5px + 1vmin);
  color: var(--text-color);
  border-radius: 15px;
  padding: 5px;
  margin: 5px;
}

.App-image-box {
  height: 13em;
  width: 10em;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  border-radius: 15px;
  margin: 5px;
}

.App-body {
  background: linear-gradient(#282c34, grey);
  color: var(--text-color);
  border-radius: 15px;

  height: 100%;
  width: 100%;
}

.Background-picture {
  display: flex;
  flex-direction: column;
  align-items: left;

  border-radius: 15px;
  background: linear-gradient(grey, #282c34);
  padding: 8px;
  margin: 8px;

  height: 38rem;
}

.Main-description {
  border-radius: 15px;

  background: linear-gradient(grey, #282c34);

  height: 15rem;

  display: flex;
  flex-direction: column;
  align-items: left;
  text-align: left;
  justify-content: center;
  font-size: 0.95vw;

  padding: 8px;
  margin: 8px;
}

.Loose-despcription {
  /* display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  font-size: calc(4px + 4vh); */

  height: 2rem;

  border-radius: 15px;
  background: linear-gradient(grey, #282c34);

  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  align-items: left;
  font-size: 0.8vw;
  padding: 8px;
  margin: 8px;
}

.App-link {
  color: --link-color;
}

.Ext-links {
  color: --link-color;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
